@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap');

body {
	background-color: #f9f2ed;
}

* {
	font-family: 'Inter', system-ui !important;
	box-sizing: border-box;
	user-select: none;
}

.App {
	display: grid;
	grid-column-gap: 30px;
	margin: 300px auto;
	background-color: #fff;
	padding: 30px;
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05);
	border-radius: 15px;
	@media (min-width: 767.9px) {
		grid-template-columns: 1fr 1fr;
		width: 800px;
	}
	@media (max-width: 767.9px) {
		grid-template-columns: 1fr;
		max-width: 800px;
		margin: 50px auto;
	}
}

.block {
	input {
		border: 1px solid rgba(18, 11, 11, 0.27);
		font-size: 3rem;
		font-weight: bold;
		padding: 20px 20px;
		letter-spacing: 1.3px;
		outline: none;
		margin-top: 10px;
		border-radius: 5px;
		width: 100%;

		&:focus {
			border: 2px solid rgba(23, 40, 191, 0.772);
		}
	}
	&:not(:last-child) {
		margin-bottom: 20px;
	}
}

.currencies {
	display: flex;
	list-style: none;
	padding: 0;
	margin: 0;
	border: 1px solid rgba(18, 11, 11, 0.27);
	border-radius: 5px;
	overflow: hidden;
	@media (max-width: 767.9px) {
		flex-direction: column;
		font-size: 26px;
	}
	li {
		display: inline-flex;
		padding: 10px 20px;
		border-right: 1px solid rgba(15, 11, 11, 0.411);
		cursor: pointer;
		justify-content: center;
		flex: 1;

		&:hover {
			background-color: rgba(0, 0, 0, 0.03);
		}

		&:active {
			background-color: rgba(0, 0, 0, 0.05);
		}

		&.active {
			background-color: #16b67f;
			color: #fff;
		}

		&:last-of-type {
			display: inline-flex;
			align-items: center;
			flex: 0 0 50px;
			border-right: 0;

			svg {
				width: 16px;
				height: 16px;
			}
		}
	}
}
